import React, { useState } from 'react';
import { DoggyIcon } from '../components/icons/DoggyIcon';
import { Row, Col, Spin } from 'antd';
import Rating from 'react-rating';
import Axios from 'axios';

export const Home = ({ togglePage }) => {
    const [rating, setRating] = useState(null);
    const [ratingError, setRatingError] = useState(null);
    const [name, setName] = useState('');
    const [nameError, setNameError] = useState('');
    const [feedback, setFeedback] = useState('');
    const [feedbackError, setFeedbackError] = useState('');
    const [inputSelected, setInputSelected] = useState(false);
    const [textAreaSelected, setTextAreaSelected] = useState(false);
    const [sendingRequest, setSendingRequest] = useState(false);
    const [submitButtonText, setSubmitButtonText] = useState("Send feedback");
    const [serverErrorText, setServerErrorText] = useState('');

    let submitFeedback = () => {
        if (!rating) {
            setRatingError('Please rate your experience!');
        }
        if (!name) {
            setNameError('Oops! Your forgot to fill out this field');
        }
        if (!feedback) {
            setFeedbackError('Oops! Your forgot to fill out this field');
        }
        if (feedback && rating && name) {
            setSendingRequest(true);
            setServerErrorText('')
            Axios.post('/feedback', {
                rating,
                name,
                feedback,
            })
                .then(res => {
                    setSendingRequest(false);
                    setSubmitButtonText("Thank you for your feedback!");
                    togglePage();
                })
                .catch(err => {
                    setServerErrorText(
                        'There was an error talking to the server, please try again',
                    );
                    setSendingRequest(false);
                    setSubmitButtonText("Send Feedback")
                    console.log(err);
                });
        }
    };

    let handleRating = e => {
        setRating(e);
        setRatingError(null);
    };

    let handleName = e => {
        setName(e.target.value);
        setNameError(null);
    };

    let handleFeedback = e => {
        setFeedback(e.target.value);
        setFeedbackError(null);
    };

    return (
        <div className="App">
            <Row className="top-container">
                <Col span={24} md={10} style={{ minWidth: 450, maxWidth: 768 }}>
                    <DoggyIcon style={{ marginBottom: 20, height: 200 }} />
                    <div className="feedback-form-title">Feedback form</div>
                    <div className="feedback-header-container">
                        <div className="feedback-header-title">Send us your feedback!</div>
                        <div className="feedback-header-description">
                            Do you have a suggestion or found some bug? Let us know in the field below
                        </div>
                    </div>
                </Col>
            </Row>
            <Row className="bottom-container">
                <Col span={24} md={10} style={{ minWidth: 450, maxWidth: 768 }}>
                    <div className="rating-container">
                        <div className="rating-title">How was your experience?</div>
                        <div style={{ display: 'flex', alignItems: 'center' }}>
                            <Rating onClick={handleRating} initialRating={rating} />
                            <div className="error-text">{ratingError}</div>
                        </div>
                        <div
                            className={
                                inputSelected ? 'name-title-active' : nameError ? 'title-error' : 'experience-title'
                            }
                        >
                            Name:
                        </div>
                        <input
                            className="name-input"
                            onFocus={() => setInputSelected(true)}
                            onBlur={() => setInputSelected(false)}
                            onChange={handleName}
                        />
                        <div
                            className={
                                textAreaSelected
                                    ? 'experience-title-active'
                                    : feedbackError
                                    ? 'title-error'
                                    : 'experience-title'
                            }
                        >
                            Describe your experience:
                        </div>

                        <textarea
                            onFocus={() => setTextAreaSelected(true)}
                            onBlur={() => setTextAreaSelected(false)}
                            onChange={handleFeedback}
                        />
                        <button onClick={submitFeedback} className="submit-button">
                            {sendingRequest ? <Spin /> : submitButtonText}
                        </button>
                        <div className="error-text">{serverErrorText}</div>
                    </div>
                </Col>
            </Row>
        </div>
    );
};
