import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import './App.css';
import { Home } from './pages/Home';
import { Nav } from './components/Nav';
import { FeedbackPage } from './pages/FeedbackPage';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentPage: 'submit',
        };
    }

    onTogglePage = () => {
        this.setState({
            currentPage: this.state.currentPage === 'submit' ? 'feedback' : 'submit',
        });
    };

    render() {
        const App = () => (
            <div>
                <Nav currentPage={this.state.currentPage} togglePage={this.onTogglePage} />
                {this.state.currentPage === 'submit' ? <Home togglePage={this.onTogglePage} /> : <FeedbackPage />}
            </div>
        );
        return (
            <Switch>
                <App />
            </Switch>
        );
    }
}

export default App;
