import React, { useEffect, useState } from 'react';
import Rating from 'react-rating';
import Axios from 'axios';

export const FeedbackPage = () => {
    const [feedbackArray, setFeedbackArray] = useState([]);
    const [feedbackText, setFeedbackText] = useState("Loading feedback!")

    useEffect(() => {
        Axios.get('/feedback')
            .then(res => {
                res.data.length > 0
                    ? setFeedbackText('')
                    : setFeedbackText('There is currently no feedback!');
                setFeedbackArray(res.data);
            })
            .catch(err => {
                setFeedbackText('There was an issue fetching the feedback, try reloading the page')
                console.log(err);
            });
    }, []);

    return (
        <div className="feedback-page">
            <div className="feedback-page-title">See the feedback and suggestions that you guys have sent us!</div>
            {feedbackArray.length > 0 ? (
                feedbackArray.map(({ name, feedback, rating }, index) => {
                    return (
                        <div className="feedback-container" key={index}>
                            <div className="card-name">{name}</div>
                            <div className="card-rating">
                                <Rating readonly initialRating={rating} />
                            </div>
                            <div className="card-container">{feedback}</div>
                        </div>
                    );
                })
            ) : (
                <div className="feedback-page-title">
                    {feedbackText}
                </div>
            )}
        </div>
    );
};
